import { electronics } from "../../productArray";

const initialState =  [
  {
    name: "Facemask size 2",
    price: 10,
    id: 9,
    img: "https://i.imgur.com/66GvBo2.jpg",
    category: "face masks"
  },
  {
    name: "Facemask size 4",
    price: 12,
    id: 10,
    img: "https://i.imgur.com/2UDq1uL.jpg",
    category: "face masks"
  },
  {
    name: "face mask size3",
    price: 13,
    id: 11,
    img: "https://i.imgur.com/v1Yb6Tz.jpg",
    category: "face masks"
  }
]; 

 const chartReducer = (state = initialState, action) =>{

  switch(action.type){
    case "ADD_TO_CART":
      return{
        ...state,
        carts: [...state.carts, action.payload]
      }
    case "REMOVE_FROM_CART":
        return{
          ...state,
          carts: state.carts.filter( (product) => product.id !== action.payload )
        }
     break;
    default :
    return state;
  }
}

export default chartReducer;